@value tablet, desktop from '../Variables.css';

.projectSelectors {
}

.desktop {
  display: none;
}
.projectsRoot {
  z-index: 1;
  padding-top: 0em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  margin-bottom: 1em;
  font-size: .7em;
}

.mobile {
  width: 100%;
  text-align: center;
  padding-bottom: 1em;
}

.projectRoot {
  cursor: pointer;
}

.githubLogo {
  display: none;
}

@-webkit-keyframes spin {
    from {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
    to {-webkit-transform:rotate(359deg);transform:rotate(359deg);}
}

@keyframes spin {
    from {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
    to {-webkit-transform:rotate(359deg);transform:rotate(359deg);}
}

.projectDisplay {
  position: absolute;
  right: 0;
  max-width: 100%;
  margin-top: 2em;
}

.circle {
  display: none;
}

@media only screen and (min-width: tablet) {
  .desktop {
    display: block;
  }
  .projectsRoot {
    z-index: 1;
    padding-top: 5em;
    font-size: 1em;
  }

  .projectRoot {
    cursor: pointer;
  }

  .mobile {
    display: none;
  }

  .circle {
    display: block;
  }

  .githubLogo {
    position: absolute;
    left: -27em;
    top: auto;
    margin-top: 8em;
    -webkit-animation: spin 30s infinite linear;
            animation: spin 30s infinite linear;
    -webkit-transform-origin: 300px 300px;
        -ms-transform-origin: 300px 300px;
            transform-origin: 300px 300px;
    display: block;
  }

  .projectDisplay {
    position: absolute;
    margin-top: 0em;
    right: 0;
    max-width: 40%;
  }
}

@media only screen and (min-width: desktop) {
  .projectDisplay {
    position: absolute;
    right: 0;
    max-width: 55%;
  }
}



