#waves {
  position: absolute;
  bottom: 0;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  z-index: -1;
}

.groundWrapper {
  overflow-x: hidden;
}

.waterBackground {
  background: rgb(2,0,36);
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(2,0,36,1)), color-stop(0%, rgba(0,0,0,1)), color-stop(95%, rgba(135,224,253,1)), to(rgba(135,224,253,1)));
  background: -webkit-linear-gradient(bottom, rgba(2,0,36,1) 0%, rgba(0,0,0,1) 0%, rgba(135,224,253,1) 95%, rgba(135,224,253,1) 100%);
  background: -o-linear-gradient(bottom, rgba(2,0,36,1) 0%, rgba(0,0,0,1) 0%, rgba(135,224,253,1) 95%, rgba(135,224,253,1) 100%);
  background: linear-gradient(0deg, rgba(2,0,36,1) 0%, rgba(0,0,0,1) 0%, rgba(135,224,253,1) 95%, rgba(135,224,253,1) 100%);
  height: 300vh;
  bottom: 0;
  width: 100%;
}

.el {
  background: #87e0fd;
  background-color: #87e0fd;
  color: #87e0fd;
  -ms-flex-positive: 1;
      flex-grow: 1;
  height: 20vh;
  padding: 0;
  margin: 0;
  -ms-flex-pack: stretch;
      justify-content: stretch;
  outline: 10px solid #87e0fd;
  z-index: -1;
}

.line {
  border-top: 2em solid #87e0fd;
  background-color: #87e0fd;
  z-index: 1;
  width: 1em;
  bottom: 0;
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 100%;
}



