@value tablet, desktop from './Variables.css';

span {
  display: -ms-inline-flexbox;
  display: inline-flex;
  color: white;
}

.space {
  display: block;
}

.text-root {
  display: none;
}

.text-root-mobile {
  font-size: 1em;
  letter-spacing: 1em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 1em 0 0 0;
}

@media only screen and (min-width: tablet) {

  .text-root-mobile {
    display: none;
  }

  .text-root {
    text-align: center;
    padding: 1em 0 0 1em;
    display: -ms-inline-flexbox;
    display: inline-flex;

    span {
      display: -ms-inline-flexbox;
      display: inline-flex;
      color: white;
    }
  }
}



