@value tablet, desktop, mobile from '../Variables.css';

.container {
  margin-top: 34em;
  /*centers in the container*/
  text-align: center;
}

.container svg {
  margin: 26vw auto;
  height: 40vw;
  width: 100%;
  left: 0;
}

div.circTxt, div.circTxt2 {
  /*allows for centering*/
  display: inline-block;
  /*adjust as needed*/
  color: whitesmoke;
}

@-webkit-keyframes spin {
    from {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
    to {-webkit-transform:rotate(359deg);transform:rotate(359deg);}
}

@keyframes spin {
    from {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
    to {-webkit-transform:rotate(359deg);transform:rotate(359deg);}
}

div.circTxt2 {
  padding-top: 13vw;
  font-size: .8em;
  -webkit-animation: spin 30s infinite linear;
          animation: spin 30s infinite linear;
  -webkit-transform-origin: 0vw 47vw;
      -ms-transform-origin: 0vw 47vw;
          transform-origin: 0vw 47vw;
}

div.circTxt {
  font-size: 1em;
  -webkit-animation: spin 50s infinite linear;
          animation: spin 50s infinite linear;
  -webkit-transform-origin: 0vw 45vw;
      -ms-transform-origin: 0vw 45vw;
          transform-origin: 0vw 45vw;
}

div.circTxt2 p {
  height: 30vw;
}

div.circTxt p {
  height: 40vw;
}

div.circTxt2 {
  position: absolute;
}

@media only screen and (min-width: mobile) {
  .container {
    margin-top: 45em;
    /*centers in the container*/
    text-align: center;
  }

  div.circTxt2 {
    padding-top: 14vw;
    font-size: 1.5em;
  }

  div.circTxt {
    font-size: 2em;
  }

  div.circTxt2 p {
    height: 30vw;
  }

  div.circTxt p {
    height: 40vw;
  }
}

@media only screen and (min-width: tablet) {
  .container {
    margin-top: 60em;
    /*centers in the container*/
    text-align: center;
  }

  .container svg {
    margin: 19vw auto;
    height: 28vw;
    width: 100%;
    left: 0;
  }

  div.circTxt2 {
    padding-top: 11vw;
    font-size: 2em;
    -webkit-transform-origin: 0vw 33vw;
        -ms-transform-origin: 0vw 33vw;
            transform-origin: 0vw 33vw;
  }

  div.circTxt {
    font-size: 2.6em;
    -webkit-transform-origin: 0vw 33vw;
        -ms-transform-origin: 0vw 33vw;
            transform-origin: 0vw 33vw;
  }

  div.circTxt2 p {
    height: 20vw;
  }

  div.circTxt p {
    height: 30vw;
  }
}



